.page {
  display: flex;
  margin: 0;
  padding: 0;

  &__inner {
    display: flex;
    flex-direction: row;
    list-style: none;

    @media (min-width: $screen-xs) {
      margin: 0 auto;
      padding: 0;
    }

    @media (min-width: $screen-sm) {
      margin: 0;
      padding-left: 30px;
    }

    @media (min-width: $screen-lg) {
      margin: 0;
      padding-left: 60px;
    }
  }

  &__list {
    margin-right: 22px;
    padding: 5px 10px;
    border-radius: 60%;
    font-weight: 700;
    font-size: $font-size-s;



    &:hover {
    background-color: $pink-light;
    color: $pink;
    transition: all 0.3s;
    }

    &:active {
      color: $pink;
      background-color: transparent;

      &:active a {
      color: $gray;
      background-color: transparent;
      }
    }
  }



  &__link {
    color: $text-color;
    background-color: transparent;

  }


  &__separator {
    margin: 0;
    padding-right: 18px;
  }

  .j-current {
    background-color: $pink-light;
    color: $pink;
  }
}
